<template>
	<view class="view-content-comment" style="margin-bottom: 100rpx;">
		<view v-if="commentList.length > 0" class="view-comment-list" v-for="(item, index) in commentList" :key="index">
			<!-- 图像 -->
			<image :src="item.avatar" class="view-comment-list-item-photo" mode="aspectFill"></image>
			<view class="view-comment-list-item">
				<view class="comment-title">
					<text class="comment-name">{{ item.username }}</text>
					<view style="flex: 1;"></view>
					<image
						v-if="item.IS_PRAISE == 1"
						class="praise-icon"
						@click="clickPraise(item)"
						src="data:image/png;base64,"
						mode="aspectFill"
					></image>
					<image
						v-else
						class="praise-icon"
						@click="clickPraise(item)"
						src="data:image/png;base64,"
						mode="aspectFill"
					></image>
					<text class="praise-num" style="margin-left: 10rpx;">{{ item.PRAISE_NUM }}</text>
				</view>
				<text class="comment-content">{{ item.content }}</text>
				<view class="comment-sub">
					<text class="comment-sub-time">{{ item.create_time }}</text>
					<view class="comment-sub-recomment" style="color: #999999;" v-if="item.self == 1" @click="clickDelete(item)">删除</view>
					<view class="comment-sub-recomment" @click="clickRecomment(item)">回复</view>
				</view>
				<view class="comment-child" v-show="item.child_comment_list.length > 0">
					<view class="comment-child-item" v-for="(itemChild, indexChild) in item.child_comment_list" :key="indexChild">
						<text class="comment-child-item-name" @click="clickRecommentChild(itemChild)">{{ itemChild.username + '：' }}</text>
						<text class="comment-child-item-content" v-if="itemChild.other_username">回复</text>
						<!-- <text class="comment-child-item-content">{{ itemChild.username }}</text> -->
						<text class="comment-child-item-content">{{ itemChild.other_username ? itemChild.other_username : '' }}</text>
						<text class="comment-child-item-content" v-if="itemChild.other_username">：</text>
						<text class="comment-child-item-content">{{ itemChild.content }}</text>
						<text v-if="itemChild.self == 1" class="comment-child-item-delete" @click="clickDeleteChild(itemChild)">删除</text>
					</view>
				</view>
			</view>
		</view>
		<block v-if="commentList.length == 0">
			<u-empty text="暂无评论" mode="message"></u-empty>
		</block>
	</view>
</template>

<script>
export default {
	name: 'MulCommentList',
	//属性
	props: {
		commentList: {
			type: Array
		},
	},
	data() {
		return {};
	},
	onLoad: function(e) {},
	onShow: function() {
		console.log('最新评论', this.title, this.commentList);
	},
	methods: {
		/**
		 * 点赞
		 * @param {Object} item
		 */
		clickPraise(item) {
			this.$emit('clickPraise', item);
		},
		/**
		 * 回复 评论
		 * @param {Object} item
		 */
		clickRecomment(item) {
			this.$emit('clickRecomment', item);
		},
		/**
		 * 删除评论
		 * @param {Object} item
		 */
		clickDelete(item) {
			this.$emit('clickDelete', item);
		},
		/**
		 * 删除评论
		 * @param {Object} item
		 */
		clickDeleteChild(item) {
			this.$emit('clickDeleteChild', item);
		},
		/**
		 * 回复评论的评论
		 * @param {Object} item
		 */
		clickRecommentChild(item) {
			this.$emit('clickRecommentChild', item);
		}
	}
};
</script>

<style lang="scss">
// @import '@/common/css/iconfont.css';
.view-content-comment {
	background-color: white;
	margin-top: 20rpx;
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 30rpx;
}
.praise-icon {
	height: 40rpx;
	width: 40rpx;
	font-size: 36rpx;
	color: #999;
}
.praise-num {
	font-size: 26rpx;
	color: #999;
}


.border-line {
	border-bottom: 1px solid #f6f6f6;
}
.view-comment-list {
	width: 100%;
	display: flex;
	flex-direction: row;
	background-color: white;
	padding: 20rpx 0;
	border-bottom: 1px solid #f6f6f6;
}
.view-comment-list-item-photo {
	height: 80rpx;
	width: 80rpx;
	border-radius: 40rpx;
	border: 1px solid #f6f6f6;
}
.view-comment-list-item {
	margin-left: 16rpx;
	display: flex;
	flex: 1;
	flex-direction: column;
}
.comment-name {
	color: #666;
	font-size: 32rpx;
	display: flex;
	align-items: center;
}
.comment-title {
	align-items: center;
	display: flex;
	height: 60rpx;
	flex-direction: row;
}

.comment-content {
	color: #000000;
	font-size: 32rpx;
	font-weight: 500;
}
.comment-sub {
	display: flex;
	align-items: center;
	flex-direction: row;
	.comment-sub-time {
		color: #999;
		font-size: 26rpx;
	}
	.comment-sub-recomment {
		color: #666;
		font-size: 28rpx;
		padding: 0 0 0 20rpx;
	}
}
.comment-child {
	background-color: #f8f8f8;
	border-radius: 10rpx;
	padding: 0 20rpx;
}
.comment-child-item {
	flex-wrap: wrap;
	margin: 15rpx 0;
	display: flex;
	align-items: center;
	flex-direction: row;
	.comment-child-item-name {
		white-space: nowrap;
		font-size: 30rpx;
		color: #5074cf;
	}
	.comment-child-item-content {
		font-size: 30rpx;
		text-align: center;
	}
	.comment-child-item-delete {
		font-size: 28rpx;
		color: #999999;
		margin-left: 30rpx;
		text-align: center;
	}
}
</style>
